.lypop-option-sum { display: flex; }

.lypop-option-sum-title { flex: 1; padding-left: 10px; font-size: 14px; color: #666; }

@media (min-width: 992px) { .lypop-option-sum-title { padding-left: 20px; font-size: 16px; } }

.lypop-option-sum-number { flex: 1; padding-right: 10px; font-size: 35px; color: #333; font-weight: bold; text-align: right; white-space: nowrap; }

@media (min-width: 992px) { .lypop-option-sum-number { padding-right: 20px; font-size: 25px; } }

.lypop-option-sum-number .text { margin-left: 3px; font-size: 20px; vertical-align: 3px; }

@media (max-width: 992px) { .lypop-option .lypop-btn { display: flex; margin-top: 14px; padding: 12px 10px; border-top: 1px solid #d1d1d1; background-color: #e6e6e6; }
  .lypop-option .lypop-btn .lypop-btn-black, .lypop-option .lypop-btn .lypop-btn-white { flex: 1; padding: 13px 20px; border-width: 2px; font-size: 22px; } }

.lypop-price-right { display: block; margin: 0; padding: 0; text-align: right; }

.lypop-shipper { color: #666; }

.lypop-review { margin: 0 10px; border: 1px solid #dcdde0; }

@media (min-width: 992px) { .lypop-review { margin: 0 20px; } }

.lypop-review-head { height: 33px; border-bottom: 1px solid #dcdde0; }

.lypop-review-head button { position: relative; height: 100%; padding: 0 18px; border: 0px; background-color: transparent; color: #666; cursor: pointer; }

.lypop-review-head button.on:before { content: ""; position: absolute; top: -1px; left: -1px; right: -1px; height: 2px; background-color: #666; }

.lypop-review-body { height: 74px; }

@media (min-width: 992px) { .lypop-review-body { height: 104px; } }

.lypop-review-view, .lypop-review-write { width: 100%; height: 100%; }

.lypop-review-write textarea { width: 100%; height: 100%; padding: 10px; border: 0px; resize: none; }

.lypop-review-view-text { overflow-x: hidden; overflow-y: auto; width: 100%; height: 100%; padding: 10px; box-sizing: border-box; }

@media (max-width: 992px) { .lypop-coupon-sum { display: flex; width: 100%; font-size: 13px; color: #666; } }

.lypop-coupon-sum-title { flex: 1; padding: 0; margin: 0; font-size: inherit; }

.lypop-coupon-sum-number { flex: 1; text-align: right; }

.lypop-coupon .table-type4 { border-width: 1px; }

.lypop-coupon .table-type4 .table-cell { padding: 8px 7px; vertical-align: middle; }

.lypop-coupon .table-type4 .xe-select-box { line-height: 8px; }

@media (min-width: 992px) { .lypop-coupon-number { padding: 0 20px; } }

.lypop-coupon-number .cart-number { width: 100%; }

.lypop-coupon-number .cart-number-sum .cart-number-text { font-size: 24px; }

@media (min-width: 992px) { .lypop-coupon-number .cart-number-sum .cart-number-text { position: absolute; bottom: 13px; word-break: break-all; } }

.lypop-coupon-number .cart-number-icon { width: 20px; height: 20px; left: -15px; font-size: 20px; }

@media (min-width: 992px) { .lypop-coupon-number .cart-number-icon { width: 30px; height: 30px; left: -15px; font-size: 30px; } }
